.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 480px) {
    width: 480px;
  }
  @media screen and (min-width: 768px) and (max-width: 1079px) {
    width: 730px;
  }
  @media screen and (min-width: 1080px) {
    width: 1080px;
  }
}

.header {
    background: #315DFA;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
font-family: 'Roboto';
font-style: normal;
font-weight: 700;
font-size: 17px;
line-height: 16px;
text-transform: uppercase;
color: #AFDCE6;
padding-top: 14px;
padding-bottom: 13px;

}

.subtitle {

font-family: 'Roboto';
font-style: normal;
font-weight: 700;
font-size: 10px;
line-height: 16px;
color: #AFDCE6;
}


